    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: white;
}

#icedCoffee {
    /* border : solid 1px blue; */
    width: 30%;
    padding: 5%;
}

#arrow {
    position: absolute;
    width: 5%;
